.carousel {
  margin-top: 20px;
  margin-bottom: 20px;
}
  .carousel .carousel-item {
    max-height: 600px; 
    width: 100%;
    height: auto;
  }
  
#carousel-btn {
  background-color: #ee8572;
  right: 0px;
  position: absolute;
  bottom: 50px;
  z-index: 1; 
}
/*Tiles*/
.tiles {
  padding-top: 20px;
  justify-content: center;
  position: relative;
}
div.tiles +div {
  justify-content: center;
  margin-bottom: 50px;
}

.tile {
  
  cursor: pointer;
  overflow: hidden;
  width: 200px;
  height: 200px;
  margin-bottom: 15px;
  padding-left: 1px;
}
 .tile img {
  width: 100%;
  transition: transform 500ms ease;
}
 .tile img:hover { 
  transform: scale(1.1);
}
  
/*quote Machine*/
.social-bar{
  padding: 0;
  list-style-type: none;
  text-align: center;
  margin-bottom: 20px;
}
.social-bar li{
  
  display: inline;
  
}
.quote-box{
  text-align: center;
}

/*about page*/

.about{
  padding-top: 30px;
  padding-bottom: 50px;
}

/*ScrollToTopBtn*/
.scroll-to-top{
  position: fixed;
  bottom: 0.5rem;
  right:0.5rem;
  animation: fadeIn 700ms ease-in-out 1s both;
  cursor: pointer;
}